<template>
  <article class='notFound'>
    <div class='notFound__wrapper'>
      <div class='notFound__content'>
        <h1 class='notFound__title'>404</h1>
        <h3 class='notFound__subtitle'>Упс, Страница не найдена</h3>
        <p class='notFound__text'>Страница, которую вы ищете удалена или URL изменился.</p>
        <div>
          <router-link to="/" tag="button" class='notFound__btn'>Вернуться на главную</router-link>
        </div>
      </div>
    </div>
  </article>
</template>

<script>
    export default {
        name: "NotFound"
    }
</script>

<style lang="scss" scoped>
  .notFound {
    &__wrapper {
      width: 100%;
      min-height: 100vh;
      color: #fff;
      background-color: #000;
      display: flex;justify-content: center;
      align-items: center;
    }
    &__content {
      text-align: center;
      position: relative;
      top: -11vh;
    }
    &__title {
      font-family: Hardcore;
      font-weight: bold;
      font-size: 10em;
      margin: 1.5rem;
      letter-spacing: .1em;
    }
    &__subtitle,
    &__text {
      text-transform: uppercase;
      /*font-family: "Oswald - Regular Italic";*/
      font-weight: 400;
      font-family: Hardcore;
      font-size: 1.75rem;
    }
    &__subtitle {
      font-size: 2.5rem;
    }
    &__btn {
      border: none;
      background-color: #fff;
      color: #000;
      font-family: Hardcore;
      font-size: 3rem;
      padding: .75rem 2.25rem;
      border-radius: 15px;
      margin: 2rem;
      cursor: pointer;
      box-shadow: 4px 3px 15px 1px rgba(255, 255, 255, 0.4),
                  inset 0 0 22px 2px rgba(255, 255, 255, 0.2);
      outline: none;
      opacity: .9;
      position: relative;
      &:hover {
        opacity: 1;
      }
      &:active {
        opacity: 1;
        box-shadow: none;
        left: 4px;
        top: 3px;
      }
    }
  }
</style>



